// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui';             //element-ui
import 'element-ui/lib/theme-chalk/index.css';  //element-uicss
import router from './router'
import App from './App'
import Axios from "axios"
import qs from "qs"
import store from "./store" 
import $ from "jquery"
import { Loading } from 'element-ui'
import toast from './components/toast/index'
import 'default-passive-events'

Vue.use(toast)
Vue.use(ElementUI);   //elementUI

Vue.config.productionTip = false


Vue.prototype.HOST = "http://k56.kaidongyuan.com/tmsApp/"
Vue.prototype.HOST = "http://139.9.180.214:9000/rest/apigateway/"


//http
Vue.prototype.httpRequest = function (url, params, success){

  var that = this

  console.log(that.HOST + url)

  var loading = Loading.service({
    lock: true,
    text: '...',
    spinner: 'el-icon-loading',
    background: 'rgba(0, 0, 0, 0.7)'
  })

  Axios.post(that.HOST + url, {"params": JSON.stringify(params)})
  .then(function(res){

    console.log(res)

    loading.close()

    if(res.data.code == 0){

      success(res.data)

    }else{

      var message
      if(res.data.message){

        message = res.data.message

      }else{

        message = url + ""
      }
      that.$toast({message:message, type:'', position:'bottom', duration:'2000'})
    }
  })
  .catch(function(res){

    console.log(res)
    loading.close()
    that.$toast({message:"  " + res, type:'', position:'bottom', duration:'3000'})
  })
}

//http
Vue.prototype.httpRequest_token = function (url, params, success){

  var that = this

  console.log(that.HOST + url)

  var loading = Loading.service({
    lock: true,
    text: '...',
    spinner: 'el-icon-loading',
    background: 'rgba(0, 0, 0, 0.7)'
  })

  if(!store.state.token){
    router.push('/');
  }

  Axios.post(that.HOST + url, 
    {
      "params": JSON.stringify(params)
    },
    {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        "Authorization": store.state.token
      }
    }
  ).then(function(res){

    console.log(res)

    loading.close()

    if(res.data.code == 0){

      success(res.data)

    }else{

      var message
      if(res.data.message){

        message = res.data.message

      }else{

        message = url + ""
      }
      that.$toast({message:message, type:'', position:'bottom', duration:'2000'})
    }
  })
  .catch(function(res){

    console.log(res)
    console.log(res.message)
    loading.close()

    if(that.$store.state.request_timed_out_list.indexOf(url) == -1){
      console.log("")
      that.$store.state.request_timed_out_list.push(url)
    }else{
      console.log("")
    }
    console.log("")
    console.log(that.$store.state.request_timed_out_list)
    that.$toast({message:"  " + res, type:'', position:'bottom', duration:'3000'})
  })
}

